body {
  margin: 0;
  padding: 0;
  background-color: #ffc700;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.field {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 90%;
  margin: 5px;
  font-size: 20px;
}

.field__label {
  margin-bottom: 5px;
  text-align: start;
}

.field__input {
  border-radius: 3px;
  font-size: 18px;
}

@media (min-width: 768px) {
  .field {
    width: 60%;
  }
}

@media (min-width: 992px) {
  .field {
    width: 50%;
  }
}

@media (min-width: 1200px) {
  .field {
    width: 40%;
  }
}

.text {
  margin: 40px 0 20px 0;
  font-size: 20px;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.logo {
  height: 40vmin;
  pointer-events: none;
}

.footer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 50px;
  font-size: 12px;
  text-transform: uppercase;
}

.red {
  color: #E91E63;
}

.line {
  width: 80%;
  background-color: gray;
  height: 1px;
}

.app {
  width: 100%;
  height: 100%;
  text-align: center;
}

